<!-- 赛事计划 -->
<template>
    <main id="plan" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key.keyword" placeholder="请输入赛事名称">
                <el-button slot="append" icon="search" @click="search">查询</el-button>
            </el-input>
            <el-button class="add-new" type="success" v-if="$store.state.orgLevel===1" @click="addEvent">预发布赛事</el-button>
            <div class="date-area">
                <span>比赛开始时间范围：</span>
                <el-date-picker
                        v-model="key.date"
                        type="daterange"
                        align="left"
                        placeholder="选择日期范围"
                        @change="formatTime"
                        :picker-options="pickerOptions">
                </el-date-picker>
                &nbsp;&nbsp;&nbsp;&nbsp;
                <el-button type="info"
                           @click="reset"
                           size="mini">重置搜索条件</el-button>
            </div>
            <div class="filter-area">
                <div class="arrow-container"
                     @click="changeSort('game')">
                    <i class="el-icon-sort-up"
                       :class="{active:key.sort==='ga'}"></i>
                    <i class="el-icon-sort-down"
                       :class="{active:key.sort==='gd'}"></i>
                </div>
                <span class="arrow-title"
                      @click="changeSort('game')">比赛时间：</span>
                <el-radio-group v-model="filter.gameTime"
                                @change="switchFilter('gameTime')"
                                size="small">
                    <el-radio-button v-for="item in timeArr"
                                     :label="item.label"
                                     :key="item.value"></el-radio-button>
                </el-radio-group>
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;发布方：</span>
                <el-radio-group v-model="filter.isPlan"
                                @change="switchFilter('isPlan')"
                                size="small">
                    <el-radio-button v-for="item in isPlanArr"
                                     :label="item.label"
                                     :key="item.value"></el-radio-button>
                </el-radio-group>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <div class="arrow-container"
                     @click="changeSort('create')">
                    <i class="el-icon-sort-up"
                       :class="{active:key.sort==='ca'}"></i>
                    <i class="el-icon-sort-down"
                       :class="{active:key.sort==='cd'}"></i>
                </div>
                <span class="arrow-title"
                      @click="changeSort('create')">创建时间</span>
                <br>
                <br>
                <div class="arrow-container"
                     @click="changeSort('apply')">
                    <i class="el-icon-sort-up"
                       :class="{active:key.sort==='aa'}"></i>
                    <i class="el-icon-sort-down"
                       :class="{active:key.sort==='ad'}"></i>
                </div>
                <span class="arrow-title"
                      @click="changeSort('apply')">申请时间：</span>
                <el-radio-group v-model="filter.applyTime"
                                @change="switchFilter('applyTime')"
                                size="small">
                    <el-radio-button v-for="item in timeArr"
                                     :label="item.label"
                                     :key="item.value"></el-radio-button>
                </el-radio-group>
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;授权状态：</span>
                <el-radio-group v-model="filter.checkState"
                                @change="switchFilter('checkState')"
                                size="small">
                    <el-radio-button v-for="item in stateArr"
                                     :label="item.label"
                                     :key="item.value"></el-radio-button>
                </el-radio-group>

            </div>
        </section>
        <section class="event-area">
            <p style="color:#999;margin:20px 0;" class="text-center"
               v-if="currentData.length===0">暂无数据</p>
            <div v-for="item in currentData" class="games">
                <p class="title">{{item.name}}<span v-if="item.eventId">（云比赛赛事名：{{item.lswlName}}）</span></p>
                <div class="game-info">
                    <i class="fa fa-building-o"></i><span>{{item.rank}}</span>
                    <i class="fa fa-bank"></i><span> 已申请机构数：{{item.isPlan===1?item.org:1}}</span>
                    <i class="fa fa-cny text-red" v-if="item.money"></i><span class="text-red" v-if="item.money">保证金：{{item.money}} 元</span>
                    <el-tag :type="item.isPlan===1?'primary':'warning'">{{item.isPlan===1?'省级计划':'非省级计划'}}</el-tag>
                    <el-tag v-if="item.checkState===2" type="success">已授权给：{{item.orgName}}</el-tag>
                    <el-tag v-if="item.checkState===0 && item.checkName !==''" type="info">当前审核机构：{{item.checkName}}</el-tag>
                    <el-tag v-if="item.checkState===1" type="danger">未通过</el-tag>
                    <el-button class="text-center GoGoGo"
                               type="info"
                               size="small"
                               v-if="item.eventId"
                               @click="getSSid(item.eventId)">进入赛事</el-button>
                    <el-button class="text-center"
                               type="success"
                               size="small"
                               @click="solve(item.id)">{{item.op}}</el-button>
                    <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                               class="text-center btn-edit"
                               type="primary"
                               size="small"
                               @click="edit(item.id)">编 辑</el-button>
                    <el-button v-if="item.checkState!==2&&item.isPlan===1&&$store.state.orgLevel===1"
                               class="text-center btn-del"
                               type="danger"
                               size="small"
                               @click="del(item.id,item.name)">删 除</el-button>
                </div>
                <div class="game-info">
                    <i class="fa fa-bullseye" v-if="item.applyTime[0]"></i><span v-if="item.applyTime[0]">申请时间：{{showTime(item.applyTime[0])+' 至 '+showTime(item.applyTime[1])}}</span>
                    <br>
                    <i class="fa fa-clock-o" v-if="item.gameTime[0]"></i><span v-if="item.gameTime[0]">比赛时间：{{showTime(item.gameTime[0])+' 至 '+showTime(item.gameTime[1])}}</span>
                    <i class="fa fa-flag" v-if="item.createTime"></i><span v-if="item.createTime">创建时间：{{item.createTime.substring(0,16)}}</span>
                </div>
                <p class="intro">{{item.intro}}</p>
            </div>
            <div class="text-center pagination">
                <el-pagination @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[6, 10, 20, 50]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum">
                </el-pagination>
            </div>
            <el-dialog title="删除赛事"
                       width="30%"
                       :visible.sync="delVis">
                <p>是否确认删除赛事 <span class="text-blue"> {{delName}}</span> ？</p>
                <div style="height:10px"></div>
                <p class="tip">提示：一旦删除无法恢复！</p>
                <p class="tip">已授权或有未处理信息时无法删除。</p>
                <div style="height:10px"></div>
                <div class="text-center">
                    <el-button size="small" @click="delVis = false">取 消</el-button>
                    <el-button type="primary" size="small" @click="confirmDel">确 认</el-button>
                </div>
            </el-dialog>
            <el-dialog :title=" rules.name+' - 赛事规程'"
                       :visible.sync="rulesVis">
                <div class="text-right">
                    <el-button type="primary" size="small" @click="getSSid(rules.id)">进入赛事</el-button>
                </div>
                <div style="height:10px"></div>
                <div v-html="rulesContent" v-if="rulesContent">{{rulesContent}}</div>
                <div v-if="!rulesContent" style="font-size:14px" class="text-center">暂未发布赛事规程</div>
            </el-dialog>
            <el-dialog title="绑定云比赛账号"
                       width="30%"
                       :visible.sync="bindVis">
                <p>
                    暂未绑定云比赛账号，无法使用赛事服务。
                </p>
                <p class="tip" v-if="!$store.state.total.set.auth">提示：你的账号无此权限，请使用主管理员账号绑定。</p>
                <div style="height:10px"></div>
                <div class="text-center">
                    <el-button size="small" @click="bindVis = false">取 消</el-button>
                    <el-button type="primary" size="small" @click="goToBind" v-if="$store.state.total.set.auth">前往绑定</el-button>
                </div>
            </el-dialog>
        </section>
    </main>
</template>
<script>
    export default{
        data(){
            const that = this;
            return {
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                isPlanArr:[
                    {label:'全部',value:''},
                    {label:'省级',value:'1'},
                    {label:'非省级',value:'0'},
                ],
                timeArr:[
                    {label:'全部',value:''},
                    {label:'未开始',value:'0'},
                    {label:'进行中',value:'1'},
                    {label:'已结束',value:'2'},
                ],
                stateArr:[
                    {label:'全部',value:''},
                    {label:'待审核',value:'0'},
                    {label:'未通过',value:'1'},
                    {label:'已授权',value:'2'},
                ],
                filter:{
                    isPlan:'全部',
                    gameTime:'全部',
                    applyTime:'全部',
                    checkState:'全部'
                },
                key:{
                    keyword:'',
                    date:[],
                    type:'all',
                    gameTime:'',
                    applyTime:'',
                    isPlan:'',
                    checkState:'',
                    sort:'ca'
                },
                activeName:'first',
                opText:'',
                pageSize:that.$parent.getCookie('planPageSize') !== '' ? (that.$parent.getCookie('planPageSize')>>0) : 6,
                currentPage:1,
                currentData:[],
                totalNum:0,
                isBoss:'',
                rulesVis:false,
                delVis:false,
                bindVis:false,
                editId:'',
                delName:'',
                rulesContent:'',
                rules:{
                    id:'',
                    name:''
                }
            }
        },
        methods: {
            changeSort(type){
                const that = this;
                switch (type){
                    case 'game':
                        that.key.sort = that.key.sort === 'ga' ? 'gd' : 'ga';
                        break;
                    case 'apply':
                        that.key.sort = that.key.sort === 'aa' ? 'ad' : 'aa';
                        break;
                    case 'create':
                        that.key.sort = that.key.sort === 'ca' ? 'cd' : 'ca';
                        break;
                }
                that.getData();
            },
            reset(){
                this.key = {
                    keyword:'',
                    date:[],
                    type:'all',
                    gameTime:'',
                    applyTime:'',
                    isPlan:'',
                    checkState:'',
                    sort:'ca',
                };
                this.filter = {
                    isPlan:'全部',
                    gameTime:'全部',
                    applyTime:'全部',
                    checkState:'全部',
                };
                this.getData();
            },
            goToBind(){
                const that = this;
                if(!that.$store.state.total.set.auth) return false;
                that.$router.push('/Main/setting3');
                that.$parent.addTab('绑定云比赛');
            },
            showGameRules(eid,name){
                const that = this;
                const _url = that.$store.state.gameRuleURL + eid;// 2028 有内网数据
                that.rules.id = eid;
                that.rules.name = name;
                that.$ajax({
                    url:_url
                }).then( res => {
                    const _data = res.data;
                    if( _data.error === 0 ){
                        that.rulesVis = true;
                        that.rulesContent = _data.datArr.rules;
                    }
                }, res => {
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            getSSid(eid){
                const that = this;
                const __url = that.$store.state.getSSid;
                let ssid;
                if( __url ==='' ) return false;
                that.$ajax({
                    url:__url
                }).then(res=>{
                    const _data = res.data;
                    if( (_data.errcode>>0)===0 ){
                        ssid = _data.data.ssid;
                        that.toYunbisai(ssid,eid);
                    } else {
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            toYunbisai(ssid,eid){
                const that = this;
                const __url = that.$store.state.toYunbisai;
                if( __url ==='' ) return false;
                that.$parent.openNewTag();
                window.open(that.$store.state.baseURL + __url + '?ssid=' + ssid + '&event_id=' + eid + '&type=1');
            },
            edit(id){
                const that = this;
                that.$parent.addTab('编辑赛事');
                that.$router.push('/Main/planEdit?'+id);
            },
            del(id,name){
                const that = this;
                that.delVis = true;
                that.delName = name;
                that.editId = id;
                that.delVis = true;
            },
            confirmDel(){
                const that = this;
                const __url = that.$store.state.total.event.child.eventDel[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        id:that.editId
                    }
                }).then(res=>{
                    const _data = res.data;
                    that.delVis = false;
                    if((_data.errcode>>0)===0){
                        that.$notify.success('删除成功');
                        that.getData();
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    } else {
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.delVis = false;
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            showTime(e){
                if( e!==null){
                    return e.substring(0,10);
                } else {
                    return false;
                }
            },
            getOpText(isPlan,level,state){
                const str = isPlan + '' + level + state;
                switch(str){
                    case '010':
                    case '110':
                    case '111':
                        return '去授权';
                    case '121':
                    case '120':
                    case '131':
                    case '130':
                    case '141':
                    case '140':
                        return '立即申请';
                }
                return '查看详情';
            },
            formatTime(e){
                const ttt = (t,type)=>{
                    return t.getFullYear() + '-' + (t.getMonth() + 1) + '-' + t.getDate() + ' ' +( type === 'start' ? '00:00:00' : '23:59:59');
                };
                if( e !== '' && e !== null && typeof e !== 'undefined' && e !== undefined ){
                    this.key.date[0] = ttt(e[0],'start');
                    this.key.date[1] = ttt(e[1],'end');
                } else {
                    this.key.date = ['',''];
                }
                this.getData();
            },
            getRange(num){
                if(num===58) return '全国';
                if(num===59) return '省级';
                if(num===60) return '县市级';
                if(num===61) return '地市级';
            },
            handleSizeChange(pageSize){
                document.cookie = 'planPageSize='+pageSize;
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            addEvent(){
                let root = this.$parent;
                root.addTab('预发布赛事',root);
                this.$router.push('/Main/planAdd');
            },
            solve(id){
                let that = this;
                if(that.isBoss){
                    that.$parent.addTab('授权赛事');
                    that.$router.push('/Main/planApply?' + id);
                } else {
                    that.$parent.addTab('申请赛事');
                    that.$router.push('/Main/applyFor?' + id);
                }
            },
            switchFilter(type){
                const findValue = (aim,arr) => {
                    let _value;
                    arr.forEach( (e,i) => {
                        if( e.label === aim){
                            _value =  e.value;
                        }
                    });
                    return _value;
                };
                const that = this;
                switch(type){
                    case 'isPlan':
                        that.key.isPlan = findValue(that.filter.isPlan,that.isPlanArr);
                        break;
                    case 'gameTime':
                        that.key.gameTime = findValue(that.filter.gameTime,that.timeArr);
                        break;
                    case 'applyTime':
                        that.key.applyTime = findValue(that.filter.applyTime,that.timeArr);
                        break;
                    case 'checkState':
                        that.key.checkState = findValue(that.filter.checkState,that.stateArr);
                        break;
                }
                that.getData();
            },
            getData(){
                const that = this;
                const __url =that.$store.state.total.event.child.planList[1];
                if(__url==='') return false;

                if(that.key.date === null || typeof that.key.date === 'undefined'){
                    that.key.date = ['',''];
                }
                let par = {
                    type:that.key.type,
                    page:that.currentPage,
                    pageSize:that.pageSize,
                    keywords:that.key.keyword,
                    start:that.key.date[0],
                    end:that.key.date[1],
                    check_state:that.key.checkState,
                    is_plan:that.key.isPlan,
                    apply_state:that.key.applyTime,
                    game_state:that.key.gameTime,
                    sort:that.key.sort
                };
                // 去除无意义的传参
                for(let i in par){
                    if(typeof par[i] === 'undefined' || par[i] === '' ){
                        delete par[i];
                    }
                }
                that.$ajax({
                    url:__url,
                    params:par
                }).then(res=>{
                   const _data = res.data;
                   if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.currentData = [];
                        dat.forEach((e,i)=>{
                            that.currentData.push({
                                id:e.id,
                                name:e.title,
                                rank:that.getRange(e.scale>>0),
                                money:e.bail===null?0:(e.bail-0).toFixed(2),
                                isPlan:(e.is_plan>>0),
                                checkState:(e.check_state>>0),
                                gameTime:[e.game_start,e.game_end],
                                applyTime:[e.apply_start,e.apply_end],
                                intro:e.condition,
                                org:e.num===null?0:e.num,
                                lswlName:e.lswl_event_title,
                                eventId:e.event_id,
                                op:that.getOpText(e.is_plan>>0,that.$store.state.orgLevel,e.check_state>>0),
                                orgName:e.org_name,
                                checkName:e.check_org_name === null ? '' : e.check_org_name,
                                createTime:e.create_time
                            });
                        });
                       that.totalNum = _data.total;
                   }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                   }else if( (_data.errcode>>0) === 1700 ){
                       that.bindVis = true;
                   } else {
                        that.$message.warning(_data.msg);
                   }
                },res=>{
                    that.$message.warning('网络似乎出了点问题');
                });
            },
            switchTab(e){
                const that =this;
                if(e.index==='0'){
                    that.key.type = 'all';
                }else if(e.index==='1'){
                    that.key.type = 'yes';
                }else if(e.index==='2'){
                    that.key.type = 'no';
                }else if(e.index==='3'){
                    that.key.type = 'wait';
                }
                that.getData();
            },
            search(){
                this.getData();
            }
        },
        mounted(){
            const that = this;
            that.isBoss = that.$store.state.orgLevel===1;

            if(!that.isBoss) that.opText = '前往申请';

            if(that.$store.state.orgLevel){
                that.getData();
            } else {
                setTimeout(()=>{
                    that.getData();
                },1000);
            }
        }
    }
</script>
<style>
    #plan .text-right{
        text-align:right;
    }
    #plan .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #plan .games .game-info button.GoGoGo{
        top:80px;
    }
    #plan .tip{
        color:#8492A6;
        font-size:12px;
    }
    #plan .text-blue{
        color:#20a0ff;
        font-size:16px;
    }
    #plan .date-area,#plan .filter-area{
        margin-top:20px;
        color:#8492A6;
        font-size:14px;
    }
    #plan .plan{
        width:100%;
    }
    #plan .text-red{
        color:#ff4949;
    }
    #plan .pagination{
        padding-bottom:20px;
    }
    #plan .games{
        padding:6px 20px;
        margin-bottom:10px;
        position: relative;
    }
    #plan .games .intro{
        font-size:14px;
        color:#999;
        width: 90%;
        max-width: 1034px;
        margin:10px 0 15px 0;
    }
    #plan .games .game-info{
        font-size:14px;
        margin:12px 0;
    }
    #plan .games .game-info .fa{
        margin-right:8px;
    }
    #plan .games .game-info button{
        position: absolute;
        right:40px;
        top:10px;
    }
    #plan .games .game-info .btn-edit{
        top:50px;
    }
    #plan .games .game-info .btn-del{
        top:90px;
    }
    #plan .games .game-info span{
        margin-right:30px;
    }
    #plan .games .game-info .el-button span{
        margin-right:0;
    }
    #plan .games .title{
        color:#20a0ff;
        margin:10px 0;
        text-align:left;
    }
    #plan .games .title .fa{
        font-size:22px;
        margin-left:8px;
    }
    #plan .el-icon-more {
        margin-left:14px;
    }
    #plan .search-area .add-new{
        margin-left:20px;
    }
    #plan .search-area .search-group{
        width:80%;
    }
    #plan .event-area{
        margin-top:20px;
        background:#fff;
        overflow:hidden;
    }
    #plan .games{
        border:1px solid #D3DCE6;
    }
    #plan .el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
        -webkit-border-radius:0;
        -moz-border-radius:0;
        border-radius:0;
        border-top:2px solid #20a0ff;
    }
    #plan .el-tabs__item{
        box-sizing:content-box;
    }
    #plan .el-tabs__nav-scroll{
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        background:transparent;
    }
    #plan .games .game-info .el-tag{
        margin-right:10px;
    }
    #plan .arrow-container{
        width:32px;
        cursor:pointer;
        display:inline-block;
        padding:0;
        margin-right:-10px;
    }
    #plan .el-icon-sort-up,#plan .el-icon-sort-down{
        color:#c3dce6;
        font-size:18px;
        margin:-3px;
    }
    #plan .filter-area::selection{
        background:#fff;
    }
    #plan .arrow-container::selection{
        background:#fff;
    }
    #plan .filter-area::-moz-selection{
        background:#fff;
    }
    #plan .arrow-container::-moz-selection{
        background:#fff;
    }
    #plan .el-icon-sort-up.active,#plan .el-icon-sort-down.active{
        color:#20a0ff;
        font-size:20px;
    }
    #plan .arrow-title{
        cursor:pointer;
    }
</style>


